function Episodes({value}){
  const [realVal,getReal]=useState(["Loading"]);
  useEffect(async ()=>{
    fetch("https://jikan1.p.rapidapi.com/anime/"+value+"/episodes", {
  "method": "GET",
  "headers": {
    "x-rapidapi-host": "jikan1.p.rapidapi.com",
    "x-rapidapi-key": "6d21b870e5msheb67be3cb5c83c4p14975cjsndf7291e88085"
  }
}).then(response=>response.json())
.then(response => {
   getReal(response.episodes);
   console.log(realVal)
   console.log(value)
})
.catch(err => {
  console.error(err);
});
},[value]);
  return (
    <div className="episodes">
      {realVal===undefined||realVal===null?"NO episodes available":realVal.map(n=>{
        return <div className="episodes-inner">
          <p className="epid">{n.episode_id}</p>
          <p>{n.title}</p>
          <p>Japanese Title: {n.title_japanese}</p>
          <p>{n.title_romanji}</p>
          <a href={n.video_url} className="link">{n.video_url==null?"No video Available":"VIDEO URL"}</a>
          <p>Aired : {n.aired}</p>
          <p>Filler : {n.filler===false?"No filler":"Yes filler"}</p>
          <p>Recap : {n.recap===false?"No recap":"Yes recap"}</p>
          <a href={n.forum_url==null?"No Rating":n.forum_url} className="link">Rate</a>
        </div>
      })}
       <div style={{width:"100%"}}>
          <p class="cp-text">
            © Copyright 2021 Monkey D L . All rights reserved.
          </p>
      </div>
    </div>
  );
}
